@import url(https://fonts.googleapis.com/css2?family=Zen+Kurenaido&family=Zen+Old+Mincho:wght@400;700;900&display=swap);

:root{--dark:#000000;
  --accent: #ce9243;
  --light:whitesmoke;
}

/* ------------------------------common elements------------------------- */




::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}


body
{
    scrollbar-width: none !important;
  letter-spacing: 1.5px;
  background-color:#000000 !important;
  background-color:var(--dark) !important; 
  color:whitesmoke !important; 
  color:var(--light) !important; 
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  padding:0 5rem;
  box-sizing: border-box !important;
}

p
{
  /* color:rgb(92, 92, 92);  */
  font-size: 1.5rem;
}

a
{
  color:rgba(95, 72, 72, 0.882) !important;
  text-decoration: none;
}

/* Every button is customised here, padding and all are as bootstrap classes */

a>button
{ 
  font-size:1.2rem;
  border:2px solid #ce9243 !important;
  border:2px solid var(--accent) !important; 
  color:#ce9243 !important; 
  color:var(--accent) !important;
  background-color: #000000 !important;
  background-color: var(--dark) !important;
  transition: all 0.1s !important;
  border-radius: 25px !important;
 }

button:hover
{
  font-size: 1.3rem !important;
}




/*----------------------------- Header Component-----------------------*/

header
{
   height:60px;
   width:100%;
   padding:1rem;
   background-color:#000000 !important;
   background-color:var(--dark) !important;
   display: flex;
   flex-flow: row !important;
   justify-content: space-between;
}

.logo
{ 
    align-self: flex-start;
}

.logo>a>h3
{  
  color:whitesmoke;
  padding:2px;
  border:3px solid whitesmoke; 
}

.logo>span
{
    font-size:0.9rem;
}

.nav-menu
{
  grid-gap:3rem;
  gap:3rem;
  display: flex;
  flex-flow: row;
  justify-content: space-around;
}



.nav-menu>a>button
{
    border:none !important;
    height:50px; 
    text-transform:lowercase; 
    text-align:right; 
    color:whitesmoke !important; 
    font-weight:900; 
}

.nav-menu>a>button>h1
{
    font-size: 3rem;
    transition: all 0.2s;
}
.nav-menu>a>button>h1:hover
{
    font-size:3.3rem !important;
}

.active-button>button>h1
{
    font-size:3.3rem !important; 
    -webkit-text-decoration:4px underline; 
            text-decoration:4px underline;
    -webkit-text-decoration-color: #ce9243;
            text-decoration-color: #ce9243;
    -webkit-text-decoration-color: var(--accent);
            text-decoration-color: var(--accent); 
}




/*----------------------------- Landing-page Component-----------------------*/


.title-span
{
    color:whitesmoke;
    color:var(--light); 
    text-transform: capitalize;
}


.landing-about
{
    margin:auto auto;
    width: 40%;
    color:grey;
    font-size:1rem;
}

.landing-div
{
    width:100%;
    padding:0 2rem;
    margin:auto auto;
    text-align: center;
    /* margin-top:5rem; */
}
.landing-buttons
{
    margin:auto auto;
    width:30%;
}

.landing-name-div
{
    margin: auto auto;
    text-align: center;
}


.hello

{
    font-weight: 900;
    font-size:15rem !important;
    color:rgba(128, 128, 128, 0.178) !important;

}

.landing-title
{
    font-size:3rem;
    font-weight: 900;
    
}

.landing-title-span
{
    color:#ce9243;
    color:var(--accent);
}



/*----------------------------- About Component-----------------------*/


.about
{
    padding:2rem;
    margin-top:6rem;
}

.section-title>h1
{
    font-size: 3rem;
    padding-left:1rem;
    margin-bottom:3rem;
    border-left: 1px solid #ce9243;
    border-left: 1px solid var(--accent);
}

.col-one
{
    margin-top: 4rem;
    width:100%;
}

.col-two
{
    margin-top: 4rem;
    width: 100%;
}

.about-me
{
    display:flex;
    flex-flow:row;
    grid-gap:8rem;
    gap:8rem;
}

.about-me>img
{
    
    border:2px solid #ce9243;
    
    border:2px solid var(--accent);
    border-radius: 50%;
    height:10%;
    width:15%;
}
.about-flex-div
{
    display: flex;
    flex-flow:row !important;
    justify-content: space-between;
    flex-wrap: wrap;
}

.about-card-div
{
    width:50%;
   text-align: left !important;
}

.details
{
    margin-top: 3rem;
    line-height: 2rem;
    width:60%;
    font-size:1.2rem !important;
}

.email, .social
{
    font-size: 1rem;
}


.rajeev
{
    color:#ce9243;
    color:var(--accent);
}

.about-cards-body
{
    padding:3rem;
}

.about-card
{
    text-align: center;
    width:43%;
    height:auto !important;
    color:whitesmoke;
}

.cards-title
{
    color:whitesmoke !important;
}

.about-para
{

    font-size:1rem !important;
}

.card-lists>h5
{
    font-size: 0.8rem !important;
}

.sub-title
{
    color: #ce9243;
    color: var(--accent);
    font-size: 1.1rem !important;
}

.cards-text
{
  font-size:1rem; 
  font-weight: bold; 
  color:rgb(105, 105, 105);
} 

.progress-div
{
    
    width:50%;
}

.progress-inner-div
{
    margin-top: 10rem;
}

              .lang{font-size:0.8rem; width:10.7rem;margin-right: 1rem; text-align: right;}
              .skill-name
              {display:flex;
                flex-flow:row;margin-bottom: 30px;}

              .progress
              {background-color:rgb(202, 202, 202); height:0.3rem; width:100% !important;}

              .progress-bar
              {background-color:#ce9243;background-color:var(--accent);}

              .bar1{width:80%;}
              .bar2{width:75%;}
              .bar3{width:60%;}
              .bar4{width:30%;}

              .fa-html5{color:#e34c26;}
              .fa-css3{color:#264de4;}
              .fa-js{color:#f0db4f;}
              .fa-react{color:#7cc5d9;}


#cta-div
{
    margin-top:10rem;
    width:100%;
    text-align: center !important;
}

#cta-div>p
{
    font-size: 1.5rem;
    color: #ce9243;
    color: var(--accent);
}

#cta-div>h1>a
{
    font-size: 4rem;
    color: whitesmoke !important;
    color: var(--light) !important;
}



/*----------------------------- portfolio Component-----------------------*/

.portfolio
{
    padding:2rem;
    margin-top:8rem;
}

.project-card
{   
    border:1px solid rgb(36, 36, 36);
    background-color:black;
    height:auto !important;
    text-align: center;
    width:100%;
}

.project-card-body
{
    display:flex;
    flex-flow:row;

}

.project-img-div
{
    margin:auto auto;
    width:70%
}

.project-img-div>img
{
    width:100%;
}

.project-details
{
    /* padding:5rem; */
    width:20%;
}

.project-details>p
{  margin: auto auto;
    font-size:1rem;
    text-align: left;
    color:grey;
    /* width:30%; */
}

.project-details>a>button
{
    margin-bottom:2rem;
}


/*----------------------------- contact Component-----------------------*/
.contact
{
    padding:2rem;
    margin-top:6rem;
}

.contact-inner-div
{
    margin:auto auto;
    
    width:70%;
}
.query-section
{
    margin:auto auto;
    text-align: center;
}




input, textarea 
{
    background-color: black !important;
    color:whitesmoke !important;
}

.query-section>h3>i
{
    color:#ce9243 !important;
    color:var(--accent) !important;
}

.query-section>p
{
    font-size:0.8rem;
    letter-spacing: 0.5rem;
}

@media screen
  and (max-device-width: 986px)
{
          body
          {
            padding:0px !important;
          }

          header
          {
            position:fixed;
            top:0;
            height:80px;
          }

          .nav-menu
          {
            grid-gap:1rem;
            gap:1rem;
            
          }

          .nav-menu>a>button>h1
          {
            font-size: 1rem;
          }

          .nav-menu>a>button>h1:hover
          {
              font-size:1.5rem !important;
          }
          
          .active-button>button>h1
          {
              font-size:1.5rem !important;  
              -webkit-text-decoration:4px underline;  
                      text-decoration:4px underline;
              -webkit-text-decoration-color: var(--accent);
                      text-decoration-color: var(--accent);
          }
          





          /* ----------------landing-page---------- */

          .landing-page
          {
            margin-top: 10rem;
          }
          
          .hello
          {
            /* display: none; */
            margin-bottom:3rem;
            font-size: 30vw !important;
          }


          .landing-title
          {
            margin: auto auto;
            width:80%;
          }
          .landing-buttons
        {
            width:100%;
        }

          /*------------- about page----------- */
          .about{padding:1rem !important;}  
          .about-me
            { 
              margin-top:3rem;
              flex-flow: column-reverse !important;
              grid-gap:0rem;
              gap:0rem;
              justify-content: center;
              align-items: center;
            }
        .about-para
        {
          padding:0px !important;
        }
            .details{width:93%;}
            .about-me>img
            {
              height:30%;
              width:50%;
            }

            .about-flex-div
            { width:100%;
              flex-flow: column !important;
            }

            .about-card-div
            {width:100%;
              margin-top:4rem;
            text-align: center !important;}
              .about-cards-body
              {
                
                padding:1rem !important;
              }
              
            .about-card
            {width:50%;
              /* text-align: left !important; */
            }

            .sub-title
            {color:var(--accent);
              font-size: 1rem !important;
            }

            .progress-div
            {
              width:100%;
            }
            .progress-inner-div
        {
            margin-top:5rem;
        }

        #cta-div>p
        {
            font-size: 1rem;
            
        }

        #cta-div>h1>a
        {
            font-size: 2rem;
        }

            /*--------------- portfolio-page----------- */
            .project-card{height:auto !important;}
            .project-card-body
          {
            display:flex;
            flex-flow:column-reverse;
            }
            
            .project-details
            {
              width:100%;
            }

              /*--------------- contact-page---------- */

              .query-section
              {
                margin-top: 5rem;
              }
              .contact-inner-div
        {
            margin:auto auto;
            
            width:100%;
        }

}
 



 
